.queryBox {
  max-width:100%;
  display: inline-block;
  margin-left:auto;
  margin-right:auto;
  text-align:right;
  overflow:hidden;
  white-space: nowrap;
}

.searchIcon {
    margin-right:5px;
    margin-top:6px;
}

.textInnerBox {
    float:right;
}

.queryContainer {
    padding: 15px;
    border: 3px solid black;
    background-color: #fff;
    margin: 10px;
    border-radius: 20px;
}

/*.queryContainerA {
    padding: 15px;
    border: 3px solid black;
    background-color: #fff;
    margin: 10px;
    border-radius: 20px;
}*/

.progressBar {
    width: 75px;
}

.cursor {
    animation:blinkingCursor 1.0s infinite;
    font-size: 16pt;
    margin-left: -2px;
    margin-right: -2px;
    font-weight: 200; 
}

@keyframes blinkingCursor{
    0% { opacity: 1; }
    49% { opacity: 1; }
    50% { opacity: 0; }
   100% { opacity: 0; }
}

